<template>
  <el-dialog
    custom-class="upload-video-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
  >
    <!-- header -->
    <template #title>
      <span>
            
      </span>
    </template>

    <!-- content -->
    <div>
      <div>
         .
      </div>
      <div>
        <el-form :model="state.form" :rules="state.rules" ref="uploadVideoForm" :label-position="state.form.align">
          <el-form-item prop="schedule" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="state.form.schedule"
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="content" label="">
          </el-form-item>
          <input
            type="file"
            accept="video/*"
            multiple
          >
        </el-form>
      </div>
    </div>

    <!-- footer -->
    <template #footer>
      <span>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="uploadVideo"></el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { reactive, computed, ref } from 'vue'

export default {
  name: 'create-schedule-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const uploadVideoForm = ref(null)

    const state = reactive({
      form: {
        align: 'left',
        content: '',
        schedule: '',
      },
      rules: {
        schedule: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        content: [
          { required: true, message: '  .', trigger: 'blur'}
        ]
      },
      dialogVisible: computed(() => props.open),
    })

    const handleClose = function () {
      state.form.content = ''
      state.form.schedule = ''
      emit('closeUploadVideoDialog')
    }

    const uploadVideo = function () {
      console.log('')
      handleClose()
    }

    return { state, handleClose, uploadVideoForm, uploadVideo }
  }
}
</script>

<style>
.upload-video-dialog {
  width: 700px;
  height: 600px;
}
</style>
