<template>
  <el-dialog
    custom-class="create-article-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
  >
    <!-- header -->
    <template #title>
      <span>
            
      </span>
    </template>

    <!-- content -->
    <div>
      <div>
         .
      </div>
      <div>
        <el-form :model="state.form" :rules="state.rules" ref="createArticleForm" :label-position="state.form.align">
          <el-form-item prop="uploadDate" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="state.form.uploadDate"
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="title" label="" :label-width="state.formLabelWidth" >
            <el-input v-model="state.form.title" autocomplete="off" placeholder=" ."></el-input>
          </el-form-item>
          <el-form-item prop="content" label="" :label-width="state.formLabelWidth">
            <el-input
              v-model="state.form.content"
              maxlength="500"
              placeholder=" ."
              show-word-limit
              rows="10"
              type="textarea"
              resize="none"
            >
            </el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- footer -->
    <template #footer>
      <span>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="createArticle"></el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
import jwt_decode from 'jwt-decode'

export default {
  name: 'create-article-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    const createArticleForm = ref(null)

    const state = reactive({
      form: {
        align: 'left',
        content: '',
        title: '',
        uploadDate: '',
      },
      rules: {
        schedule: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        content: [
          { required: true, message: '  .', trigger: 'blur'}
        ]
      },
      dialogVisible: computed(() => props.open),
    })

    const handleClose = function () {
      state.form.content = ''
      state.form.uploadDate = ''
      state.form.title = ''
      emit('closeCreateArticleDialog')
    }

    const createArticle = function () {
      const token = store.getters['root/getJWTToken']
      const writer = jwt_decode(token).sub
      const payload = {
        'body': {
          'writer': writer,
          'teamName': props.info.teamName,
          'title': state.form.title,
          'content': state.form.content,
          'uploadDate': state.form.uploadDate
        }
      }
      // console.log(' ')
      // console.log(payload)

      store.dispatch('root/addArticle', payload)
      .then(function (result) {
        console.log('  ')
        emit('createArticle')
        swal({
          title: " ",
          text: " .",
          icon: "success",
          button: "",
        })
      })
      .catch(function (error) {
        console.log('  ')
      })
      handleClose()
    }

    return { state, handleClose, createArticleForm, createArticle }
  }
}
</script>

<style>
.create-schedule-dialog {
  width: 700px;
  height: 600px;
}
</style>
